<template>
  <div class="movieWrap">
    <div class="movieTitle">SF/판타지</div>
    <div class="sort">
      <ul>
        <li @click="newSort">최신순</li>
        <li @click="oldSort">오래된순</li>
        <li @click="abcSort">가나다순</li>
      </ul>
    </div>
    <div class="movieList">
      <div class="moviePoster" v-for="(a, i) in mPlus" :key="i">
        <div @click="modalO(i)" class="movieImg">
          <div class="movieContent">
            <h4>{{ movieSection[i].title }}</h4>
          </div>
          <img :src="movieSection[i].image2" alt="" />
        </div>
      </div>
    </div>
    <div @click="plus" v-if="show" class="moviePlue">+</div>
    <div @click="minus" v-else class="moviePlue">-</div>
  </div>
  <div class="modalWrap" v-if="modalShow">
    <Modal @modalX="modalShow = false" :push="push" :movie="movieSection"></Modal>
  </div>
</template>

<script>
import movieSection from "../assets/movieSection";
import Modal from "./Modal";

export default {
  name: "Seperation",
  data() {
    return {
      movieOrigin: [...movieSection],
      movieSection: movieSection,
      length: movieSection.length,
      mPlus: 8,
      show: true,
      count: 0,
      push: 0,
      modalShow: false,
    };
  },
  watch: {
    modalShow: function(e){
      if(!e){
        document.body.style.overflow = 'visible'
      }
    }
  },
  methods: {
    plus() {
      if (this.mPlus < this.length && this.length - this.mPlus >= 8) {
        this.mPlus = this.mPlus + 8;
        if (this.mPlus == this.length) {
          this.show = false;
        }
      } else {
        this.mPlus = this.mPlus + this.length - this.mPlus;
        if (this.mPlus == this.length) {
          this.show = false;
        }
      }
    },
    minus() {
      this.mPlus = 8;
      this.show = true;
    },
    newSort() {
      this.movieSection = [...this.movieOrigin];
    },
    oldSort() {
      this.movieSection = [...this.movieOrigin];
      this.movieSection.reverse();
    },
    abcSort() {
      this.movieSection.sort(function (a, b) {
        if (a.title < b.title) return -1;
        if (b.title < a.title) return 1;
        return 0;
      });
    },
    modalO(i) {
      this.modalShow = true;
      this.push = i;
      document.body.style.overflow = "hidden";
    },
  },
  components: {
    Modal: Modal,
  },
};
</script>

<style>
.movieWrap {
  padding: 120px 0;
  margin-top: 72.27px;
  color: #f3f3f3;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.movieWrap h3 {
  color: #f3f3f3;
  font-size: 26px;
  padding: 20px 0;
}

.movieWrap .movieList {
  font-size: 0;
  display: flex;
  flex-wrap: wrap;
}

.movieWrap .movieList .moviePoster {
  display: flex;
  width: 270px;
  position: relative;
  margin-bottom: 100px;
  padding-right: 40px;
}

.movieWrap .movieTitle {
  font-weight: 700;
  font-size: 20px;
}

.movieWrap .sort {
  margin-left: auto;
  margin-top: 80px;
  margin-bottom: 30px;
}

.movieWrap .sort li {
  display: inline-block;
  color: gray;
  padding: 4px;
  margin-left: 20px;
  cursor: pointer;
}

.movieWrap .moviePlue {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  line-height: 46px;
  border: 2px solid gray;
  color: gray;
  font-weight: 700;
  font-size: 20px;
  cursor: pointer;
  transition: 0.2s;
}

.movieWrap .moviePlue:hover {
  border: 2px solid #f3f3f3;
  color: #f3f3f3;
}

.movieWrap .movieList .moviePoster:nth-child(4n) {
  padding-right: 0;
}

.movieWrap .movieList .moviePoster .movieImg {
  width: 270px;
  height: 400px;
  transition: 0.4s;
}

.movieWrap .movieList .moviePoster .movieImg:hover {
  width: 270px;
  height: 400px;
  scale: 1.04;
}

.movieWrap .movieList .moviePoster .movieImg .movieContent {
  color: #f3f3f3;
  position: absolute;
  bottom: 0px;
  padding: 20px;
  width: 240px;
  z-index: 1;
  cursor: pointer;
}

.movieWrap .movieList .moviePoster .movieImg::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  height: 100%;
  width: 270px;
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.9) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  cursor: pointer;
}

.movieWrap .movieList .moviePoster .movieImg > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.movieWrap .movieList .moviePoster .movieImg .movieContent h4 {
  font-size: 20px;
}

.movieWrap .movieList .moviePoster .movieImg .movieContent p {
  font-size: 16px;
  margin: 0;
}
</style>