          </div>

          <div class="menuList">
            <a href="">
              <router-link to='/마이페이지'>
              <li @click='mypage'
                @mouseover="mouseEffect"
                @mouseleave="mouseLeave"
                :style="color"
              >
                마이페이지
              </li>
              </router-link>
            </a>
            <a href="">
              <router-link to='/seperation'>
              <li @mouseover="mouseEffect" @mouseleave="mouseLeave">
