</template>

<script>
import Login from "./Login.vue";
export default {
  name: "main-header",
  data() {
    return {
      isShow: false,
      menu: {
        opacity: "0",
        right: "0px",
        visibility: "hidden",
      },
      color: {
        color: "gray",
      },
      span1: {
        alignSelf: "flex-start",
        rotate: "0",
        transform: "translateY(0)",
      },
      span2: {
        alignSelf: "center",
        margin: "8px 0",
        display: "block",
      },
      span3: {
        alignSelf: "flex-end",
        rotate: "0",
        transform: "translateY(0)",
      },
      index: 0,
      mouseover: 0,
      loginShow: false,
    };
  },

  methods: {
    showHide() {
      if (!this.isShow) {
        this.menu.opacity = "1";
        this.menu.right = "300px";
        this.menu.visibility = "visible";
        this.span1.alignSelf = "center";
        this.span1.rotate = "45deg";
        this.span1.transform = "translateY(1px)";
        this.span2.display = "none";
        this.span3.alignSelf = "center";
        this.span3.rotate = "-45deg";
        this.span3.transform = "translateY(-1px)";
        this.isShow = !this.isShow;
      } else {
        this.menu.opacity = "0";
        this.menu.right = "0";
        this.menu.visibility = "hidden";
        this.span1.alignSelf = "flex-start";
        this.span1.rotate = "0deg";
        this.span1.transform = "translateY(0)";
        this.span2.display = "block";
        this.span3.alignSelf = "flex-end";
        this.span3.rotate = "0deg";
        this.span3.transform = "translateY(0)";
        this.isShow = !this.isShow;
      }
    },
    mouseEffect() {},
    mouseLeave() {},
    login() {
      this.loginShow = true;
      document.body.style.overflow = "hidden";
    },
    mypage(e){
      alert('로그인 후 이용하실 수 있습니다.')
      e.preventDefault();
    }
  },
  components: {
    Login: Login,
  },
};
</script>

