<template>
  <div class="modal">
    <div class="modalWatch">
      <img :src="movie[push].image" alt="" />
      <img @click="$emit('modalX')" class="modalClose" src="../assets/back.png" alt="" />
      <div class="play" @click="play">
        <img src="../assets/play.png" alt="" />
      </div>
    </div>
    <div class="modalContent">
      <h3>{{ movie[push].title }}</h3>
      <ul>
        <img src="../assets/Star.svg" alt="" />
        <li>{{ movie[push].score }}</li>
        <img src="../assets/time.svg" alt="" />
        <li>{{ movie[push].time }}m</li>
        <li>{{ movie[push].age }}</li>
        <li>{{ movie[push].seperation }}</li>
        <img class="down" src="../assets/down.png" alt="" />
        <li>다운로드</li>
      </ul>
      <div class="content">
        <p>{{ movie[push].content }}</p>
      </div>
      <div class="review">리뷰하기+</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Modal",
  props: {
    push: Number,
    movie: Array,
  },
  methods: {
    modalX() {
      this.modalShow = false;
      document.body.style.overflow = "visible";
      alert('asd')
    },
    play() {
      alert("지원되지 않는 기기입니다.");
    },
  },
};
</script>

<style>
.modalWrap {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100vw;
  background: rgb(0, 0, 0, 0.5);
  z-index: 99;
  left: 0;
}

.modalWrap .modal {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 670px;
  height: 690px;
  background: black;
  border: 3px solid #393939;
  border-radius: 28px;
  overflow: hidden;
}

.modalWrap .modal .modalWatch {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 0;
}

.modalWrap .modal .modalWatch::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 400px;
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(255, 255, 255, 0) 30%
  );
}

.modalWrap .modal .modalWatch::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 400px;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(255, 255, 255, 0) 30%
  );
}

.modalWrap .modal .modalWatch .play {
  position: absolute;
  width: 80px;
  height: 80px;
  background: rgb(255, 255, 255, 0.3);
  border-radius: 50%;
  cursor: pointer;
  z-index: 9;
}

.modalWrap .modal .modalWatch .play img {
  position: absolute;
  top: 50%;
  left: 52%;
  transform: translate(-50%, -50%);
  width: 26px;
  height: 32px;
}

.modalWrap .modal .modalWatch .modalClose {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 24px;
  height: 20px;
  padding: 10px;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 9;
}

.modalWrap .modal .modalWatch img {
  height: 400px;
}

.modalWrap .modal .modalContent {
  color: #f3f3f3;
  flex: 1 1 0;
}

.modalWrap .modal .modalContent ul {
  display: flex;
  align-items: center;
  padding: 6px 0 0 30px;
  font-size: 14px;
}

.modalWrap .modal .modalContent ul img {
  width: 20px;
  height: 20px;
  padding-right: 6px;
}

.modalWrap .modal .modalContent ul .down {
  margin-left: auto;
  width: 14px;
  height: 18px;
}

.modalWrap .modal .modalContent ul li {
  display: inline-block;
  margin-right: 40px;
}

.modalWrap .modal .modalContent ul li:last-child {
  cursor: pointer;
}

.modalWrap .modal .modalContent .content {
  margin: 20px 30px;
  line-height: 30px;
  font-size: 14px;
  height: 74px;
  overflow: scroll;
  overflow-x: hidden;
}

.modalWrap .modal .modalContent .content::-webkit-scrollbar {
  width: 4px;
  background: none;
}

.modalWrap .modal .modalContent .content::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background: #ad00ff;
}

.modalWrap .modal .modalContent h3 {
  font-size: 34px;
  padding: 30px 0 12px 30px;
}

.modalWrap .modal .modalContent .review {
  padding: 10px;
  width: 90px;
  margin: 0 auto;
  text-align: center;
  color: #1565ff;
  cursor: pointer;
}
</style>