<template>
  <div class="loginBox">
    <img @click="close" class="loginClose" src="../assets/back.png" alt="" />
    <div class="contentWrap">
      <img class="loginImg" src="../assets/logo.svg" alt="" />
      <form action="loginForm" id="loginForm" method="get">
        <input
          v-model="loginId"
          id="id"
          class="formInput"
          name="id"
          type="text"
          placeholder="아이디를 입력하세요."
          maxlength="12"
        />
        <input
          v-model="loginPw"
          id="password"
          class="formInput"
          name="id"
          type="password"
          placeholder="비밀번호를 입력하세요."
          maxlength="20"
        />
        <button @click="loginBtn" class="btnDeco1" id="loginBtn" type="button">
          로그인
        </button>
        <div class="findJoin">
          <div id="find" class="findJoinBtn">아이디/비밀번호 찾기</div>
          <div @click="joinOn" id="join" class="findJoinBtn">회원가입</div>
        </div>
      </form>
    </div>
  </div>
  <div v-if="joinShow" class="joinBox">
    <img @click="back" class="loginClose" src="../assets/back.png" alt="" />
    <div class="contentWrap">
      <img class="loginImg" src="../assets/logo.svg" alt="" />
      <form action="joinForm" id="joinForm">
        <input
          v-model="joinId"
          id="idJoin"
          class="formInput"
          name="id"
          type="text"
          placeholder="아이디를 입력하세요."
          maxlength="12"
        />
        <p class="idChk">한글, 특수문자 제외 6-20자</p>
        <input
          v-model="joinPw"
          id="passwordJoin"
          class="formInput"
          name="pw"
          type="password"
          placeholder="비밀번호를 입력하세요."
          maxlength="20"
        />
        <p class="pwChk">문자, 숫자, 특수문자 포함 8-20자</p>
        <input
          v-model="joinPw2"
          id="passwordJoin2"
          class="formInput"
          name="pw"
          type="password"
          placeholder="비밀번호를 재입력하세요."
          maxlength="20"
        />
        <p class="pwChk2">일치하는 비밀번호를 입력해주세요.</p>
        <input
          v-model="joinEmail"
          id="emailJoin"
          class="formInput"
          name="pw"
          type="email"
          placeholder="이메일을 입력하세요."
        />
        <p class="emailChk">ex&#41; abc1234@naver.com</p>
        <button @click="joinBtn" class="btnDeco2" id="JoinBtn" type="button">
          회원가입
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "login_open",
  data() {
    return {
      loginId: "",
      loginPw: "",
      joinShow: false,
      joinId: "",
      joinPw: "",
      joinPw2: "",
      joinEmail: "",
      idOk: false,
      pwOk: false,
      pw2Ok: false,
      emailOk: false,
    };
  },
  watch: {
    joinId() {
      let idRegExp = /^[a-zA-z0-9]{4,12}$/;
      let idChk = document.querySelector(".idChk");
      if (!idRegExp.test(this.joinId) && this.joinId !== "") {
        idChk.textContent = "아이디를 형식에 맞게 입력해주세요.";
        idChk.style.color = "#ff5959";
        this.idOk = false;
      } else if (this.joinId == "") {
        idChk.textContent = "한글, 특수문자 제외 6-20자";
        idChk.style.color = "#5a5a5a";
        this.idOk = false;
      } else {
        idChk.textContent = "사용가능한 아이디입니다.";
        idChk.style.color = "#00c8ff";
        this.idOk = true;
      }
    },
    joinPw() {
      let pwRegExp =
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,}$/;
      let pwChk = document.querySelector(".pwChk");
      if (!pwRegExp.test(this.joinPw) && this.joinPw !== "") {
        pwChk.textContent = "비밀번호를 형식에 맞게 입력해주세요.";
        pwChk.style.color = "#ff5959";
        this.pwOk = false;
      } else if (this.joinPw == "") {
        pwChk.textContent = "문자, 숫자, 특수문자 포함 8-20자";
        pwChk.style.color = "#5a5a5a";
        this.pwOk = false;
      } else {
        pwChk.textContent = "사용가능한 비밀번호입니다.";
        pwChk.style.color = "#00c8ff";
        this.pwOk = true;
      }
    },
    joinPw2() {
      let pwChk2 = document.querySelector(".pwChk2");
      if (this.joinPw !== this.joinPw2 && this.joinPw2 !== "") {
        pwChk2.textContent = "비밀번호가 일치하지 않습니다.";
        pwChk2.style.color = "#ff5959";
        this.pw2Ok = false;
      } else if (this.joinPw2 == "") {
        pwChk2.textContent = "일치하는 비밀번호를 입력해주세요.";
        pwChk2.style.color = "#5a5a5a";
        this.pw2Ok = false;
      } else {
        pwChk2.textContent = "비밀번호가 일치합니다.";
        pwChk2.style.color = "#00c8ff";
        this.pw2Ok = true;
      }
    },
    joinEmail() {
      let emailRegExp =
        /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
      let emailChk = document.querySelector(".emailChk");
      if (!emailRegExp.test(this.joinEmail) && this.joinEmail !== "") {
        emailChk.textContent = "이메일을 형식에 맞게 입력해주세요.";
        emailChk.style.color = "#ff5959";
        this.emailOk = false;
      } else if (this.joinEmail == "") {
        emailChk.textContent = "ex) abc1234@naver.com";
        emailChk.style.color = "#5a5a5a";
        this.emailOk = false;
      } else {
        emailChk.textContent = "사용가능한 이메일입니다.";
        emailChk.style.color = "#00c8ff";
        this.emailOk = true;
      }
    },
  },
  methods: {
    close() {
      this.$emit("close", this.false);
      document.body.style.overflow = "visible";
    },
    back() {
      this.joinShow = false;
    },
    joinOn() {
      this.joinShow = true;
    },
    loginBtn(e) {
      // let loginBtn = document.getElementById("loginBtn");
      if (this.loginId == "" && this.loginPw !== "") {
        alert("아이디를 입력하세요.");
        e.preventDefault();
      } else if (this.loginId !== "" && this.loginPw == "") {
        alert("비밀번호를 입력하세요.");
        e.preventDefault();
      } else if (this.loginId == "" && this.loginPw == "") {
        alert("아이디와 비밀번호를 입력하세요.");
        e.preventDefault();
      } else if (this.loginId !== "admin") {
        alert("존재하지 않는 아이디입니다.");
        e.preventDefault();
      } else if (this.loginPw !== "1234") {
        alert("비밀번호가 틀립니다.");
        e.preventDefault();
      } else {
        location.href="/"
      }
    },
    joinBtn(e) {
      if (!this.idOk) {
        alert("아이디를 확인하세요.");
        document.getElementById("idJoin").focus();
        e.preventDefault();
      } else if (!this.pwOk) {
        alert("비밀번호를 확인하세요.");
        document.getElementById("passwordJoin").focus();
        e.preventDefault();
      } else if (!this.pw2Ok) {
        alert("비밀번호가 일치하는지 확인하세요.");
        document.getElementById("passwordJoin2").focus();
        e.preventDefault();
      } else if (!this.emailOk) {
        alert("이메일을 확인하세요.");
        document.getElementById("emailJoin").focus();
        e.preventDefault();
      } else {
        location.href="/"
      }
      
    },
  },
};
</script>

<style>
.loginBox {
  position: absolute;
  width: 500px;
  height: 600px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: black;
  border: 3px solid #393939;
  border-radius: 28px;
}

.joinBox {
  position: absolute;
  width: 500px;
  height: 700px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: black;
  border: 3px solid #393939;
  border-radius: 28px;
}

.loginClose {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 24px;
  height: 20px;
  padding: 10px;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 9;
}

.contentWrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loginImg {
  padding: 0 100px 30px 100px;
}

#loginForm {
  display: flex;
  flex-direction: column;
}

#joinForm {
  display: flex;
  flex-direction: column;
}

#joinForm p {
  color: #5a5a5a;
  font-size: 12px;
  padding: 0 20px;
}

.formInput {
  padding: 14px 6px;
  margin: 14px 14px 8px 14px;
  background: none;
  border: none;
  color: #f3f3f3;
  border-bottom: 1px solid gray;
}

.btnDeco1 {
  padding: 14px;
  margin: 40px 14px 14px 14px;
  background: #ad00ff;
  border: none;
  color: #f3f3f3;
  cursor: pointer;
}

.btnDeco2 {
  padding: 14px;
  margin: 50px 14px 14px 14px;
  background: #ad00ff;
  border: none;
  color: #f3f3f3;
  cursor: pointer;
}

.findJoin {
  display: flex;
  color: #f3f3f3;
  font-size: 14px;
  margin: 14px;
}

.findJoin .findJoinBtn {
  flex: 1 1 0;
  cursor: pointer;
  text-align: center;
}
</style>