<template>
  <section class="section1">
    <div class="slide">
      <div
        class="imgWrap"
        :style="{
          transform: 'translate(' + moveSlide + 'px)',
          width: movie.length * 100 + '%',
        }"
      >
        <div class="poster" v-for="(poster, i) in movie" :key="i">
          <div class="posterInfo">
            <transition name="title">
            <h2 v-if="count == i">{{ movie[i].title }}</h2>
            </transition>
            <transition name="title">
            <div v-if="count == i" class="info1">
              <img src="../assets/Star.svg" alt="" />
              <span>{{ movie[i].score }}</span>
              <img src="../assets/time.svg" alt="" />
              <span>{{ movie[i].time }}m</span>
              <span>{{ movie[i].age }}</span>
            </div>
            </transition>
            <transition name="content">
            <p v-if="count == i" class="info2">{{ movie[i].content }}</p>
            </transition>
            <transition name="content">
            <button v-if="count == i"
              @mouseover="mouseEffect"
              @mouseleave="mouseLeave"
              @click="modalO(i)"
              :style="watch"
              class="watchNow"
            >
              시청하기
            </button>
            </transition>
          </div>
          <img :src="movie[i].image" alt="" />
        </div>
      </div>
    </div>
    <button v-if="count > 0" @click="prev" class="prev">
      <img src="../assets/left.svg" alt="" />
    </button>
    <button v-else class="prev">
      <img src="../assets/left.svg" alt="" />
    </button>
    <button v-if="count < movie.length - 1" @click="next" class="next">
      <img src="../assets/right.svg" alt="" />
    </button>
    <button v-else class="next">
      <img src="../assets/right.svg" alt="" />
    </button>
  </section>
  <div class="modalWrap" v-if="modalShow">
    <Modal @modalX='modalShow = false' :push="push" :movie="movie"></Modal>
  </div>
</template>

<script>
import Modal from "./Modal";

export default {
  name: "SectionOne",
  data() {
    return {
      moveSlide: 0,
      count: 0,
      push: 0,
      watch: {
        background: "none",
      },
      modalShow: false,
    };
  },
  watch: {
    modalShow: function(e){
      if(!e){
        document.body.style.overflow = 'visible'
      }
    }
  },
  props: {
    movie: Array,
  },
  methods: {
    prev: function () {
      this.count--;
      this.moveSlide = -1200 * this.count;
    },
    next: function () {
      this.count++;
      this.moveSlide = -1200 * this.count;
    },
    mouseEffect() {
      this.watch.background = "#ad00ff";
    },
    mouseLeave() {
      this.watch.background = "none";
    },
    modalO(i) {
      this.modalShow = true;
      this.push = i;
      document.body.style.overflow = 'hidden'
    },
  },
  components: {
    Modal: Modal,
  }
};
</script>

<style>

.title-leave-from{
  opacity: 1;
  transform: translateY(0);
}
.title-leave-active{
  transition: all 1s;
}
.title-leave-to{
  opacity: 0;
  transform: translateY(-80px);
}

.title-enter-from{
  opacity: 0;
  transform: translateY(-80px);
}
.title-enter-active{
  transition: all 1s;
}
.title-enter-to{
  opacity: 1;
  transform: translateY(0);
}

.content-leave-from{
  opacity: 1;
  transform: translateY(0);
}
.content-leave-active{
  transition: all 1s;
}
.content-leave-to{
  opacity: 0;
  transform: translateY(80px);
}

.content-enter-from{
  opacity: 0;
  transform: translateY(80px);
}
.content-enter-active{
  transition: all 1s;
}
.content-enter-to{
  opacity: 1;
  transform: translateY(0);
}

.section1 {
  position: relative;
  width: 100%;
  margin-top: 72.27px;
}

.section1 .slide {
  position: relative;
  overflow: hidden;
}

.section1 .poster {
  display: inline-block;
  position: relative;
  width: 1200px;
  height: 700px;
}

.section1 .poster::before {
  content: "";
  height: 700px;
  position: absolute;
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  width: 100%;
}

.section1 .poster > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.section1 .poster .posterInfo {
  display: flex;
  position: absolute;
  flex-direction: column;
  top: 50%;
  left: 50px;
  transform: translateY(-50%);
  color: #f3f3f3;
  width: 600px;
}

.section1 .poster .posterInfo h2 {
  font-size: 50px;
  margin: 12px 0;
}

.section1 .poster .posterInfo .info1 {
  display: flex;
  align-items: center;
  font-size: 18px;
}

.section1 .poster .posterInfo .info1 span {
  width: 66px;
  padding-left: 10px;
}

.section1 .poster .posterInfo .info2 {
  font-size: 14px;
  margin: 28px 0;
  line-height: 28px;
}

.section1 .poster .posterInfo .watchNow {
  width: 160px;
  height: 58px;
  background: none;
  border: 2px solid #ad00ff;
  color: #f3f3f3;
  font-size: 20px;
  cursor: pointer;
}

.section1 > button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 50px;
  color: #f3f3f3;
  background: none;
  border: none;
  cursor: pointer;
}

.section1 .prev {
  left: -70px;
  z-index: 99;
}

.section1 .next {
  right: -70px;
  z-index: 99;
}

.section1 .imgWrap {
  transition: all 0.8s;
}

</style>