
import { createWebHistory, createRouter } from "vue-router";
import Seperation from './components/Seperation.vue'
import Seperation2 from './components/Seperation2.vue'
import Seperation3 from './components/Seperation3.vue'
import Seperation4 from './components/Seperation4.vue'
import Seperation5 from './components/Seperation5.vue'
import MyPage from './components/MyPage.vue'
import Main from './components/Main.vue'

const routes = [

    {

        path: "/seperation",

        component: Seperation,

    },
    {

        path: "/seperation2",

        component: Seperation2,

    },
    {

        path: "/seperation3",

        component: Seperation3,

    },
    {

        path: "/seperation4",

        component: Seperation4,

    },
    {

        path: "/seperation5",

        component: Seperation5,

    },
    {

        path: "/마이페이지",

        component: MyPage,

    },
    {

        path: "/",

        component: Main,

    }


];


const router = createRouter({

    history: createWebHistory(),

    routes,

});


export default router;

