<template>
  <section class="section2">
    <h3>이번 주 인기영화 TOP4</h3>
    <div class="popular">
      <div class="popPoster" v-for="(topM, i) in top" :key="i">
        <div @click="modalO(i)" class="popImg">
          <div class="popContent">
            <h4>{{ top[i].title }}</h4>
            <p>{{ top[i].seperation }}</p>
          </div>
          <img :src="top[i].image2" alt="" />
        </div>
      </div>
    </div>
  </section>
  <div class="modalWrap" v-if="modalShow">
    <Modal @modalX='modalShow = false'  :push="push" :movie="top"></Modal>
  </div>
</template>

<script>
import Modal from "./Modal";

export default {
  name: "SectionTwo",
  data() {
    return {
      count: 0,
      push: 0,
      modalShow: false,
    };
  },
  watch: {
    modalShow: function(e){
      if(!e){
        document.body.style.overflow = 'visible'
      }
    }
  },
  props: {
    top: Array,
    movie: Array
  },
  methods: {
    modalO(i) {
      this.modalShow = true;
      this.push = i;
      document.body.style.overflow = "hidden";
    },
  },
  components: {
    Modal: Modal,
  },
};
</script>

<style>
.section2 {
  padding: 50px 0;
}

.section2 h3 {
  color: #f3f3f3;
  font-size: 26px;
  padding: 20px 0;
}

.section2 .popular {
  font-size: 0;
  display: flex;
  height: 400px;
}

.section2 .popular .popPoster {
  width: 280px;
  position: relative;
  padding-right: 40px;
}

.section2 .popular .popPoster:last-child {
  padding-right: 0;
}

.section2 .popular .popPoster .popImg {
  width: 270px;
  height: 400px;
  transition: 0.4s;
}

.section2 .popular .popPoster .popImg:hover {
  width: 270px;
  height: 400px;
  scale: 1.04;
}

.section2 .popular .popPoster .popImg .popContent {
  color: #f3f3f3;
  position: absolute;
  bottom: 0px;
  padding: 20px;
  width: 240px;
  z-index: 1;
  cursor: pointer;
}

.section2 .popular .popPoster .popImg::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  height: 100%;
  width: 270px;
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.9) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  cursor: pointer;
}

.section2 .popular .popPoster .popImg > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.section2 .popular .popPoster .popImg .popContent h4 {
  font-size: 24px;
  margin: 10px 0;
}

.section2 .popular .popPoster .popImg .popContent p {
  font-size: 16px;
  margin: 0;
}
</style>